<template>
    <div class="tab-pane fade show active" id="pills-chat" role="tabpanel" aria-labelledby="pills-chat-tab">
      <CallLog  v-if="view=='call'" @viewType="listType"  v-bind:store="store"/>
      <DialPad v-if="view=='dialPaid'"  @viewType="listType" v-bind:store="store" />
    </div>
  </template>
  
  <script>
  import CallLog from '@/components/Call/CallLog.vue';
  import DialPad from '@/components/Call/DialPad.vue';
  
  export default {
    components: {
      CallLog,
      DialPad
    },
    props:{
      store:{
        type:Object
      }
    },
    data() {
        return {
            view:'call'
        };
    },
    methods: {
        listType(event){
            this.view = event
        }
    }
  }
  </script>
  

  