<template>
<div class="user-chat bg-calllog w-100 overflow-hidden">
    <div class="d-lg-flex h-100 ">
        <!-- Start chat conversation section -->
        <div class="w-100 overflow-hidden position-relative p-3 " >
            <div class="card h-100 " >
                <ul class="callloglist" >
                    <li v-for="(recentCall, index) in store.getRecentCall" :key="index">
                        <div class="col1">
                            <div class="d-flex chat-box">
                                <div class="chat-user-img online align-self-center me-3 ms-0">
                                    <img class="rounded-circle avatar-xs" :src="recentCall.USER_IMAGE ? recentCall.USER_IMAGE :'https://talygenv.github.io/Vue-UI-Content/images/UC/uc_user_new.jpg'" alt="andrew">
                                </div>
                                <div class="flex-grow-1 overflow-hidden">
                                    <h5 class="text-truncate font-size-14 mb-1">{{ recentCall.CALLER_NAME }}</h5>
                                    <p class="chat-user-message text-truncate mb-0"> {{ recentCall.CALL_TYPE=='Outgoing' ? recentCall.CALL_TO : recentCall.CALL_FROM }} </p>
                                </div>
                            </div>
                        </div>
                        <div class="col2">
                            <label>Duration</label>
                            <span class="sub-c-details">
                                <p class="text-olive" :class="recentCall.CALL_TYPE=='Outgoing' ? 'text-success': recentCall.CALL_TYPE == 'Incomming' && (typeof recentCall.START_TIME === 'undefined' || recentCall.START_TIME == null) ? 'text-danger' :'text-warning'  ">
                                    {{recentCall.CALL_TYPE == 'Incomming' && (typeof recentCall.START_TIME === 'undefined' || recentCall.START_TIME == null) 
                                            ? 'Missed Call' : recentCall.CALL_TYPE == 'Outgoing' && (typeof recentCall.START_TIME === 'undefined' || recentCall.START_TIME == null) ? recentCall.CALL_TYPE+ ' - Not Picked' : secondsToHms(recentCall.DURATION)  }}</p>
                            </span>
                        </div>
                        <div class="col3">
                            <label>Type</label>
                            <div class="typecall"><span :class="recentCall.CALL_TYPE == 'Incomming' ? 'icon icon-received': recentCall.CALL_TYPE == 'Outgoing' ? 'icon icon-received' : 'icon icon-miss' ">
                                    <i :class="recentCall.CALL_TYPE == 'Incomming' ? 'ri-arrow-left-down-line': recentCall.CALL_TYPE == 'Outgoing' ? 'ri-arrow-right-up-line' : 'ri-arrow-left-down-line'"></i></span>
                                    <p  >
                                    {{recentCall.CALL_TYPE == 'Incomming' && (typeof recentCall.START_TIME === 'undefined' || recentCall.START_TIME == null) 
                                            ? 'Missed Call' : recentCall.CALL_TYPE == 'Outgoing' && (typeof recentCall.START_TIME === 'undefined' || recentCall.START_TIME == null) ? recentCall.CALL_TYPE+ ' - Not Picked' :recentCall.CALL_TYPE }}</p>
                            </div>
                        </div>
                        <div class="col4">
                            <label>Date & Time</label>
                            {{ getPreviousDay(recentCall.CREATED_AT)+' '+ getTime(recentCall.CREATED_AT) }}
                        </div>
                        <div class="col5">
                            <div class="callvideo rounded-circle handpointer" @click="redirectCall(recentCall.CALL_TYPE=='Outgoing' ? recentCall.CALL_TO : recentCall.CALL_FROM)">
                                 <i :class="recentCall.CALL_TYPE=='video' ? 'fas fa-video': 'fas fa-phone-alt'"></i> 
                            </div>
                        </div>
                    </li>
                    <div class="recent-calls-listing-wrapper scrollbar-inner scroll-content" v-if="store.getRecentCall.length==0" id="div_forRecentCallList" style="height: auto; margin-bottom: 0px; margin-right: 0px; ">
                        <div class="text-center text-danger border p-2 bg-white">{{ $t('No_Record_Found') }}</div>
                    </div>
                </ul>
            </div>
        </div>

        <!-- Start User profile detail sidebar -->

    </div>
</div>
</template>

<script>
import {
    secondsToHms,
    getPreviousDay
} from "@/assets/common/vue-common"
export default {
    props: {
        store: {
            type: Object
        }
    },
    methods: {
        getTime(date) {
            const newInputDate = date.replace(" ", "T");

            const timeStamp = new Date(newInputDate);
            return timeStamp.toLocaleTimeString(navigator.language, {
                hour: '2-digit',
                minute: '2-digit'
            })
        },
        redirectCall(event) {
            if (this.store.$state.callToken) {
                this.dailNumber = event
                //this.makeCallPopupShow = true
            } else {
                this.ShowAlert(this.$t("ConnectDevice"), "failure", true, this.$t("Alert"));
            }

        },
    },
    data() {
        return {
          dailNumber:''
        };
    },
    setup() {
        return {
            secondsToHms,
            getPreviousDay
        };
    }
};
</script>
