<template>
    <div class="settingaccordion">
      <div class="px-3 pt-3">
        <h4 class="mb-4 d-flex justify-content-between align-items-center">
          Call Log 
          <span class="dialpad"> 
            Dial Pad 
            <span class="gridview handpointer" @click="$emit('viewType','dialPaid')"><i class="fas fa-th" ></i></span> 
            <!-- <span class="listview" @click=""><i class="fas fa-list" ></i></span> -->
          </span>
        </h4>
      </div>
  
      <div class="px-3 pt-0">
        <div id="settingprofile" class="accordion">
          <AccordionItem 
            id="allcall" 
            title="All Calls" 
            :expanded="false"

          >
            <div class="accordion-body p-2">
              <CallLogItem text="All" @click="callType('All')"/>
              <CallLogItem text="Missed Calls" @click="callType('MissedCalls')"/>
              <CallLogItem text="Rejected Calls" @click="callType('RejectedCalls')"/>
              <CallLogItem text="Outgoing Calls" @click="callType('OutgoingCalls')"/>
              <CallLogItem text="Incoming Calls" @click="callType('IncomingCalls')"/>
            </div>
          </AccordionItem>
  
          <AccordionItem 
            id="searchdate" 
            title="Search by Date" 
            :expanded="false"
           
          >
            <div class="accordion-body p-2">
              <div class="row mb-2">
                <div class="col-md-6">
                  <label>From</label>
                  <input type="date" class="form-control" v-model="fromDate" @change="validateDates"/>
                </div>
                <div class="col-md-6">
                  <label>To</label>
                  <input type="date" class="form-control" v-model="toDate" @change="validateDates"/>
                </div>
                <div v-if="dateError" class="error">{{ dateError }}</div>
                <div class="col-md-12 mt-3">
                  <button type="submit" class="btn btn-primary" @click="search">Search</button>
                </div>
              </div>
            </div>
          </AccordionItem>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import AccordionItem from './AccordionItem.vue';
  import CallLogItem from './CallLogItem.vue';
  
  export default {
    components: {
      AccordionItem,
      CallLogItem
    },
    props:{
      store:{
        type:Object
      }
    },
    data() {
      return {
        callLogExpend:false,
        fromDate: '',
        toDate: '',
        dateError:''
      };
    },
    methods: {
      search() {
        if (!this.dateError) {
        let from = ''
        let to = ''
        if (this.fromDate && this.toDate) {
                from = this.fromDate + " 00:00:00"
                to = this.toDate + " 23:59:59"
            }
            this.store.recentCalls(`fromDate=${encodeURIComponent(from)}&toDate=${encodeURIComponent(to)}&isLocalSearch=false`)
          }
      },
      callType(){
        this.store.recentCalls(`fromDate=&toDate=&isLocalSearch=false`)
      },
      validateDates() {
            if (this.fromDate && this.toDate) {
                if (new Date(this.toDate) < new Date(this.fromDate)) {
                    this.dateError = 'To Date cannot be earlier than From Date.';
                } else {
                    this.dateError = '';
                }
            }
        },
    }
  }
  </script>
  
  <style>
  /* Add your styles here */
  </style>
  